

import React from 'react';
import './style.scss';

const Header = () => {
    return (
        <header>
            <h1>RESTy</h1>
            <nav>
                <ul>
                    <li>home</li>
                    <li>about</li>
                </ul>
            </nav>
        </header>
    )
}

const Footer = () => {
    return (
        <footer>
            <p>HA-HA!</p>
        </footer>
    )
}

const modeChange = (e, mod) => {
    e.preventDefault();
    console.log(this.state)
    this.setState({mode: mod})
}

const Rester = () => {
    return (
        <form>
            <input type='text' placeholder="Address" value={this.state.url} />
            <div className="button-box">
            <button type='submit'>submit</button>
            <button onClick={e=> this.props.modeChange(e, 'GET')}>GET</button>
            <button onClick={e=> this.props.modeChange(e, 'POST')}>POST</button>
            <button onClick={e=> this.props.modeChange(e, 'PUT')}>PUT</button>
            <button onClick={e=> this.props.modeChange(e, 'DELETE')}>DELETE</button>
            </div>
            <textarea show={this.state.mode === "PUT" || this.state.mode === "POST"} />
        </form>
    )
}

  
class App extends React.Component {

    constructor(props){
        super(props);
        this.state = {
            url:'https://',
            mode:'',

        }
    }


    render(){
  return (
      <>
    <Header />
        <Rester modeChange={this.modeChange}/>
    <Footer />
    </>
  );
}}

export default App;